<template>
  <div class="wrapper">
    <div class="title">我的订单</div>
    <div class="orders">
      <div class="order" v-for="(item, index) in list" :key="index">
        <div class="order__title">
          <span>{{ item.shopName }}</span>
          <span class="order__status">{{ item.isCanceled ? '已取消' : '已支付' }}</span>
        </div>
        <div class="order__content">
          <div class="order__content__imgs">
            <img v-for="product in item.products" :key="product.product.img" :src="product.product.img" alt="">
          </div>
          <div class="order__content__info">
            <div class="order__content__info__price">&yen;{{ item.price }}</div>
            <div class="order__content__info__count">共{{ item.count }}件</div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <Docker :currentIndex="2" />
</template>

<script>
import { reactive, toRefs } from 'vue'
import { get } from '../../utils/request'
import Docker from '../../components/Docker.vue'

const useOrderListEffect = () => {
  const data = reactive({ list: [] })
  const getOrderList = async () => {
    const result = await get('/api/order')
    if (result.errno === 0 && result.data.length) {
      const orderList = result.data
      let price = 0
      let count = 0
      orderList.forEach(orderItem => {
        orderItem.products.forEach(item => {
          price += item.product.price * item.orderSales
          count += item.orderSales
        })
        orderItem.price = price
        orderItem.count = count
      })
      data.list = orderList
    }
  }
  getOrderList()
  const { list } = toRefs(data)
  console.log(list.value)
  return { list }
}

export default {
  name: 'OrderList',
  components: { Docker },
  setup () {
    const { list } = useOrderListEffect()
    return { list }
  }
}
</script>

